import React from "react";
import { Fragment } from "react/cjs/react.production.min";
import MaterialTable from '@material-table/core';
class List extends React.Component{
    state={
        isEdit:false
    }
    courseList=()=>{
        return(
            
                <li>
                    
                    {console.log(this.props.value)}
               <span>{this.props.value.name}</span>
               <button onClick={this.props.delete}>delete</button>
               <button onClick={()=>{this.togle()}}>Edit</button>
               </li>
           
        )}
        up=(e)=>{
            e.preventDefault();
            this.props.up(this.props.index )
            
            this.togle()
        }
        update=()=>{
            console.log(this.props.value);
            
            return(
                <form onSubmit={this.up}>
                    <input type='text' onChange={this.props.x} defaultValue={this.props.value.name} />
                    <button>update</button>
                </form>

            )
            
            this.togle()
        }
        togle=()=>{
            this.setState({
                isEdit: !this.state.isEdit
            })
        }

    render(){
      return(
        < Fragment>
            {this.state.isEdit?this.update():this.courseList()}
        </ Fragment>
      )
    }
}
export default List;