import React from "react";
import Form from "./component/form";
import List from "./component/list";
class App extends React.Component{
  
  state={
    course:[
      {name:'Html'},
      {name:'Css'},
      
    ],
    current:'',
    update:''
  };
  
  update=(e)=>{
    
    this.setState({
      current:e.target.value
    })
    
  }

  add=(e)=>{
    let course=this.state.course
    let current=this.state.current
   e.preventDefault();
   course.push({name:current});
   
   this.setState({
     course:course
   })
   console.log(this.state.course)
  
  }
 delete=(e)=>{
   this.state.course.splice(e,1)
   this.setState({
     course:this.state.course
   })
 }
 up=(index)=>{
let course=this.state.course[index]
  course['name']=this.state.update
  this.setState({
    course:this.state.course
  })
 }
x=(e)=>{
this.setState({
  update:e.target.value
})
}
  render(){
  const  courses=this.state.course.map((item,index)=>{
     
      return(
        <List value={item} key={index} up={()=>{this.up(index)}} x={this.x} delete={()=>this.delete(index)}/>
      )
    })
  
    return(
      <div className='app'>
        
        <h2>Add Courses</h2>
        <Form update={this.update} add={this.add}/>
        <ul className='x'>{courses}</ul>
         
      </div>
    )
  }
}

export default App;
